nav ul,
button{
    font-size: 18px;
    font-family: "Montserrat";
    font-weight: 600;
}

nav {
  width: 90%;
  padding: 40px 0px;
  margin: auto;
  min-height: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav ul {
  display: flex;
  justify-content: space-around;
  flex: 1.5;
}
nav a{
  color: #777777;
  position: relative;
  transition: all 0.3s cubic-bezier(0.550, 0.085, 0.680, 0.530);
  -webkit-transform-origin: top;
            transform-origin: top;
}
