nav .logo-container {
  flex: 1.5 1;
  cursor: pointer
}
nav .button-container {
  flex: 1.5 1;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
#btn-signup,
#btn-login {
  border-radius: 40px;
  width: 165px;
  height: 60px;
}
#btn-signup {
  background: #ffffff;
  border: 2px solid #000000;
}
#btn-login {
  background: #ffda80;
}

